import * as React from 'react';
import { ContentPage } from '@pidoc/components';

    import { PageContent, Markdown, PageLayout } from '@pidoc/components';

    const link = "https://github.com/smapiot/pidoc/tree/develop/packages/example-app/docs/basics/05-language.md";
    const html = `
<h1 id="languages" tabindex="-1">Languages</h1>
<p>Pidoc allows you to either just have a single (unnamed) language or have multiple (named) languages in your documentation.</p>
<p>To enable support for this feature a <code>language</code> field must be added to the <em>docs.config.json</em>. This one consists of a default language selection (<code>default</code>) and all the available languages (<code>selection</code>). The latter is an object mapping the language IDs to use against their names on the docs page.</p>
<p>Additionally, the given sections may then be translated, too. Instead of simple strings (which are still allowed) you can then use objects, mapping the language IDs to their translations for the title.</p>
<p>Quick example:</p>
<pre><code class="hljs language-json"><span class="hljs-punctuation">{</span>
  <span class="hljs-comment">//...</span>
  <span class="hljs-attr">&quot;language&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-punctuation">{</span>
    <span class="hljs-attr">&quot;default&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-string">&quot;en&quot;</span><span class="hljs-punctuation">,</span>
    <span class="hljs-attr">&quot;selection&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-punctuation">{</span>
      <span class="hljs-attr">&quot;en&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-string">&quot;English&quot;</span><span class="hljs-punctuation">,</span>
      <span class="hljs-attr">&quot;de&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-string">&quot;Deutsch&quot;</span><span class="hljs-punctuation">,</span>
      <span class="hljs-attr">&quot;fr&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-string">&quot;Fran√ßais (beta)&quot;</span>
    <span class="hljs-punctuation">}</span>
  <span class="hljs-punctuation">}</span><span class="hljs-punctuation">,</span>
  <span class="hljs-attr">&quot;sitemap&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-punctuation">{</span>
    <span class="hljs-attr">&quot;basics&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-punctuation">{</span>
      <span class="hljs-attr">&quot;title&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-punctuation">{</span>
        <span class="hljs-attr">&quot;en&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-string">&quot;Basics&quot;</span><span class="hljs-punctuation">,</span>
        <span class="hljs-attr">&quot;de&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-string">&quot;Grundlagen&quot;</span><span class="hljs-punctuation">,</span>
        <span class="hljs-attr">&quot;fr&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-string">&quot;Bases&quot;</span>
      <span class="hljs-punctuation">}</span><span class="hljs-punctuation">,</span>
      <span class="hljs-attr">&quot;sections&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-punctuation">[</span>
        <span class="hljs-punctuation">{</span>
          <span class="hljs-attr">&quot;generator&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-string">&quot;markdown&quot;</span><span class="hljs-punctuation">,</span>
          <span class="hljs-attr">&quot;segment&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-string">&quot;&quot;</span><span class="hljs-punctuation">,</span>
          <span class="hljs-attr">&quot;dir&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-string">&quot;basics&quot;</span>
        <span class="hljs-punctuation">}</span><span class="hljs-punctuation">,</span>
        <span class="hljs-punctuation">{</span>
          <span class="hljs-attr">&quot;generator&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-string">&quot;markdown&quot;</span><span class="hljs-punctuation">,</span>
          <span class="hljs-attr">&quot;segment&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-string">&quot;&quot;</span><span class="hljs-punctuation">,</span>
          <span class="hljs-attr">&quot;dir&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-string">&quot;generators&quot;</span>
        <span class="hljs-punctuation">}</span>
      <span class="hljs-punctuation">]</span>
    <span class="hljs-punctuation">}</span><span class="hljs-punctuation">,</span>
    <span class="hljs-attr">&quot;customizing&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-punctuation">{</span>
      <span class="hljs-attr">&quot;title&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-punctuation">{</span>
        <span class="hljs-attr">&quot;en&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-string">&quot;Customizing&quot;</span><span class="hljs-punctuation">,</span>
        <span class="hljs-attr">&quot;de&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-string">&quot;Personalisieren&quot;</span><span class="hljs-punctuation">,</span>
        <span class="hljs-attr">&quot;fr&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-string">&quot;Personnalisation&quot;</span>
      <span class="hljs-punctuation">}</span><span class="hljs-punctuation">,</span>
      <span class="hljs-attr">&quot;sections&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-punctuation">[</span>
        <span class="hljs-punctuation">{</span>
          <span class="hljs-attr">&quot;generator&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-string">&quot;markdown&quot;</span><span class="hljs-punctuation">,</span>
          <span class="hljs-attr">&quot;segment&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-string">&quot;&quot;</span><span class="hljs-punctuation">,</span>
          <span class="hljs-attr">&quot;dir&quot;</span><span class="hljs-punctuation">:</span> <span class="hljs-string">&quot;customizing&quot;</span>
        <span class="hljs-punctuation">}</span>
      <span class="hljs-punctuation">]</span>
    <span class="hljs-punctuation">}</span>
  <span class="hljs-punctuation">}</span>
<span class="hljs-punctuation">}</span>
</code></pre>
<p>The language support then queries subdirectories using the available language IDs. For instance, if the markdown generator is supposed to look in <code>docs/</code> for markdown files, then it would look in <code>docs/fr</code> for French translations of the found markdown files. The french translation page is then exactly like the English, but with all found replacements.</p>
<p>Using the described approach the documentation is always complete. It may not be fully translated, but it remains complete and is fully consumable in every given language.</p>
`;
    const meta = {"title":"Languages","description":"Supporting multiple languages in the documentation.","audience":"Everyone","level":"Beginner","section":"Getting Started"};
  

export default () => (
  <ContentPage>
    
    <PageLayout name={"default"} meta={meta}>
      <PageContent meta={meta}>
        <Markdown content={html} link={link} editLabel={"Edit on GitHub"} />
      </PageContent>
    </PageLayout>
  
  </ContentPage>
);